      <span class="hljs-attr">options2</span>: [<span class="hljs-string">''</span>, <span class="hljs-string">''</span>, <span class="hljs-string">''</span>, <span class="hljs-string">''</span>],
      <span class="hljs-attr">checkedAll</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">indeterminate</span>: <span class="hljs-literal">true</span>
    },
    <span class="hljs-attr">methods</span>: {
      handleChange2 (val) {
        <span class="hljs-keyword">this</span>.lists2 = <span class="hljs-keyword">this</span>.checkedAll ? [].concat(<span class="hljs-keyword">this</span>.options2) : []
        <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">false</span>
      },
      handleChangeItem (val) {
        <span class="hljs-keyword">this</span>.checkedAll = (<span class="hljs-keyword">this</span>.lists2.length === <span class="hljs-keyword">this</span>.options2.length)
        <span class="hljs-keyword">this</span>.indeterminate = val.length &gt; <span class="hljs-number">0</span> &amp;&amp; val.length &lt; <span class="hljs-keyword">this</span>.options2.length
      }
