<script>
  export default {
    data () {
      return {
        checked1: false,
        checked2: false,
        checked3: false,
        checked4: false,
        checkedTest: '',
        disabled1: false,
        lists1: [],
        options1: [
          {
            label: '',
            text: '',
            checked: true,
            disabled: true
          },
          {
            label: '',
            text: '',
            checked: true
          },
          {
            label: '',
            text: '',
            disabled: true
          },
          {
            label: '',
            text: ''
          }
        ],
        lists2: [''],
        options2: ['', '', '', ''],
        checkedAll: false,
        indeterminate: true
      }
    },
    methods: {
      handleChange1 (val) {
        this.checkedTest = val
      },
      handleChange2 (val) {
        this.lists2 = this.checkedAll ? [].concat(this.options2) : []
        this.indeterminate = false
      },
      handleChangeItem (val) {
        this.checkedAll = (this.lists2.length === this.options2.length)
        this.indeterminate = val.length > 0 && val.length < this.options2.length
      }
    }
  }
</script>


